export default{
	data() {
		return {
			current: 0,
			height: 0,
			sCur: "hot",
			focus: ''
		};
	},
	created() {
		
		this.getHeight();
	},
	onShow(){
		// 用这个api可以动态修改 tabbar
		uni.setTabBarStyle({
		  color: "#969696",
		  backgroundColor: '#FFF',
		  borderStyle: 'rgba(0,0,0,0.2)',
		  selectedColor: "#79F3FE"
		})
	},
	methods: {
		getHeight(){
				const _that = this;
				uni.getSystemInfo({
				success: function (res) {
					_that.height = res.windowHeight;
				},
				
			});
		},
		onclick(e){
			this.sCur = e.target.dataset.cur
			if(this.sCur == "hot") {
				this.current = 0
			} else if(this.sCur == "Focus") {
				this.current = 1
			} else if(this.sCur == "cos") {
				this.current = 2
			} else if(this.sCur == "game") {
				this.current = 3
			}
		},
		change(e){
			if(e.target.current == 0) {
				this.sCur = "hot"
			} else if (e.target.current == 1) {
				this.sCur = "Focus"
			} else if (e.target.current == 2) {
				this.sCur = "cos"
			} else if (e.target.current == 3) {
				this.sCur = "game"
			}
		},
		search(e){
			uni.navigateTo({
				url: './../index/search?id=1'
			});
		},
		InputFocus(e) {
			console.log("123")
			this.focus = 'onFocus'
		},
		InputBlur(e) {
			this.focus = 'lostFocus'
		}
	}
}
